import React, { useEffect, useState, useContext } from 'react'
import Button from '../Button/Button'
import Button100 from '../Button/Button100'
import Input from '../Input/Input'
import cl from './s.module.scss'
import ThemeContext from '../.././context/index'



const Home = ({notify}) => {
    const [count, setCount] = useState('')

    const {theme, setTheme} = useContext(ThemeContext)

    const divideBy100 = () =>{
        var eval1 = (eval(count))/100
        setCount(count => eval1)
    }
    const getResult = () =>{
        // return eval(count)
        var eval1 = eval(count)
        setCount(count => eval1)
    }
    const deleteLast = () =>{
        var array1 = Array.from(count)
        array1.pop()
        var string1 = array1.join('')
        setCount(count => string1)
    }

    const addNew = (item) =>{
        console.log(theme)
        var array1 = Array.from(count)
        var lastitem = array1.pop()
        if(count == ''){
            notify()
            console.log("")
        }
        else if(lastitem == "*"){
            if(item == '('){
                setCount(count=>count+item)
            }
            else{
                notify()
                console.log('last symbol is *');
            }
        }
        else if(lastitem == "."){
            if(item == '('){
                setCount(count=>count+item)
            }
            else{
                notify()
                console.log('last symbol is .');
            }
        }
        else if(lastitem == "/"){
            if(item == '('){
                setCount(count=>count+item)
            }
            else{
                notify()
                console.log('last symbol is /');
            }
        }
        else if(lastitem == "-"){
            if(item == '('){
                setCount(count=>count+item)
            }
            else{
                notify()
                console.log('last symbol is -');
            }
        }
        else if(lastitem == "+"){
            if(item == '('){
                setCount(count=>count+item)
            }
            else{
                notify()
                console.log('last symbol is +');
            }
        }
        else if(lastitem == "("){
            console.log('last symbol is (');
            notify()
        }
        else if(lastitem == ")"){
            if(item == '/' || item == '*' || item == '-' || item == '+' || item == '.'){
                setCount(count=>count+item)
            }
            else{
                notify()
                console.log('last symbol is )');
            }
        }
        else{
            setCount(count=>count+item)
        }
    }

    useEffect(() => {
        console.log(count)
    }, [count]);


    

  return (
    <div 
    style={
        theme=='default'?
        {color: 'white',
        background:'black'}:
        {color: 'black',
        background:'rgb(143, 214, 255)'}
    }
    className={cl.flex}>
        
        <p 
        style ={
            theme =='default'?
            {background: 'rgb(51, 51, 51)'}
            :
            {background: 'white'}
        }
        className={cl.paragraph}>{count}</p>
        <div 
        style ={
            theme =='default'?
            {background: 'black'}
            :
            {background: 'rgb(143, 214, 255)'}
        }
        
        className={cl.grid}>

            <Button 
            style={{
                borderRadius: '405px',
                gridRowStart: '1',
                gridRowEnd: '3',
            }}
            onClick={()=>deleteLast()}
            colorbutton={theme == 'default'?'red':'redLight'}
            >x</Button>

            <Button 
            onClick={()=>setCount('')}
            colorbutton={theme == 'default'?'red':'redLight'}
            >c</Button>

            <Button 
            onClick={()=>addNew('(')}
            colorbutton={theme == 'default'?'gray':'grayLight'}
            >(</Button>

            <Button 
            onClick={()=>addNew(')')}
            colorbutton={theme == 'default'?'gray':'grayLight'}
            >)</Button>

            <Button 
            onClick={()=>setCount(count=>-count)}
            colorbutton={theme == 'default'?'gray':'grayLight'}
            >+/-</Button>

            <Button 
            onClick={()=>divideBy100()}
            colorbutton={theme == 'default'?'gray':'grayLight'}
            >%</Button>

            <Button 
            onClick={()=>addNew('/')}
            colorbutton={theme == 'default'?'orange':'orangeLight'}
            >/</Button>
            



            <Button
            onClick={()=>setCount(count=>count + '7')}
            colorbutton={theme == 'default'?'active':'activeLight'}
            >7</Button>
            <Button
            onClick={()=>setCount(count=>count + '8')}
            colorbutton={theme == 'default'?'active':'activeLight'}
            >8</Button>
            <Button
            onClick={()=>setCount(count=>count + '9')}
            colorbutton={theme == 'default'?'active':'activeLight'}
            >9</Button>
            {/* <Button 
            onClick={()=>setCount(count=>count + '/')}
            colorButton='orange'>/</Button> */}
            <Button 
            onClick={()=>addNew('*')}
            colorbutton={theme == 'default'?'orange':'orangeLight'}
            >*</Button>
            

            



            <Button
            onClick={()=>setCount(count=>count + '4')}
            colorbutton={theme == 'default'?'active':'activeLight'}
            >4</Button>
            <Button
            onClick={()=>setCount(count=>count + '5')}
            colorbutton={theme == 'default'?'active':'activeLight'}
            >5</Button>
            <Button
            onClick={()=>setCount(count=>count + '6')}
            colorbutton={theme == 'default'?'active':'activeLight'}
            >6</Button>
            <Button 
            onClick={()=>addNew('-')}
            colorbutton={theme == 'default'?'orange':'orangeLight'}
            >-</Button>
            



            <Button
            onClick={()=>setCount(count=>count + '1')}
            colorbutton={theme == 'default'?'active':'activeLight'}
            >1</Button>
            <Button
            onClick={()=>setCount(count=>count + '2')}
            colorbutton={theme == 'default'?'active':'activeLight'}
            >2</Button>
            <Button
            onClick={()=>setCount(count=>count + '3')}
            colorbutton={theme == 'default'?'active':'activeLight'}
            >3</Button>
            
            <Button 
            onClick={()=>addNew('+')}
            colorbutton={theme == 'default'?'orange':'orangeLight'}
            >+</Button>
            


            <Button 
            colorbutton={theme == 'default'?'active':'activeLight'}
            style={{
                width: '83%',
                maxWidth: '100%',
                borderRadius: '405px',
                gridColumnStart: '1',
                gridColumnEnd: '3',
            }}
            onClick={()=>setCount(count=>count + '0')}
            >0</Button>

            <Button
            onClick={()=>addNew('.')}
            colorbutton={theme == 'default'?'active':'activeLight'}
            >.</Button>

            <Button 
            onClick={()=>getResult()}
            colorbutton={theme == 'default'?'orange':'orangeLight'}
            >=</Button>





            
        </div>

    </div>
  )
}

export default Home