import React from 'react'
import cl from './Button.module.scss'

const Button = (props) => {
    // const classNameNew = props.classNameAdd
    // var classNameNew = 'props.' + String(props.colorbutton)
    // var classNameNew = 'cl.'+ String(props.colorbutton)
    // var classNameNew1 = String(props.colorbutton)
    // console.log(classNameNew);

  return (
    <button className={     
        // props.colorbutton?
        // cl.classNameNew1:
        // cl.active   
        props.colorbutton == 'gray'
        ?
        cl.gray
        :
            (
            props.colorbutton == 'orange'
            ?
            cl.orange
            :
            (
                props.colorbutton == 'red'
                ?
                cl.red
                :
                (
                    props.colorbutton == 'grayLight'
                    ?
                    cl.grayLight
                    :
                    (props.colorbutton == 'orangeLight'
                    ?
                    cl.orangeLight
                    :
                    (
                    props.colorbutton == 'redLight'
                    ?
                    cl.redLight
                    :
                    (props.colorbutton == 'activeLight'
                    ?
                    cl.activeLight
                    :
                    cl.active
                    )
                    )
                    )
                )
                // cl.active
            )
            )
    } {...props}></button>
  )
}

export default Button